import './App.css';
import Draggable from "react-draggable";
import React, {Component} from "react";

import _ from "lodash";

function generateUUID() { // Public Domain/MIT
    var d = new Date().getTime();//Timestamp
    var d2 = ((typeof performance !== 'undefined') && performance.now && (performance.now() * 1000)) || 0;//Time in microseconds since page-load or 0 if unsupported
    return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
        var r = Math.random() * 16;//random number between 0 and 16
        if (d > 0) {//Use timestamp until depleted
            r = (d + r) % 16 | 0;
            d = Math.floor(d / 16);
        } else {//Use microseconds since page-load if supported
            r = (d2 + r) % 16 | 0;
            d2 = Math.floor(d2 / 16);
        }
        return (c === 'x' ? r : (r & 0x3 | 0x8)).toString(16);
    });
}

function smallUUID() {
    const x = generateUUID();
    return x.substring(0, 8);
}

function Arrow({fromRef, toRef}) {
    if (!fromRef || !fromRef.current || !toRef || !toRef.current) {
        return null;
    }

    const {x: fromX, y: fromY, height: fromHeight, width: fromWidth} = fromRef.current.getBoundingClientRect();
    const {x: toX, y: toY, height: toHeight, width: toWidth} = toRef.current.getBoundingClientRect();

    const arrowStartX = fromX + fromWidth;
    const arrowStartY = fromY + fromHeight / 2;

    const arrowEndX = toX + toWidth / 2;
    const arrowEndY = toY;

    const tension = 150;

    return <svg style={{position: "absolute", left: "0px", top: "0px", zIndex: -1}}
                width="100%" height="100%">
        <defs>
            <marker id="triangle" viewBox="0 0 10 10" refX="0" refY="5"
                    markerUnits="strokeWidth" markerWidth="10"
                    markerHeight="8" orient="auto">
                <path d="M 0 0 L 10 5 L 0 10 z"></path>
            </marker>
        </defs>
        {/*<circle cx={arrowStartX} cy={arrowStartY} r="5" fill="#456" stroke="none"/>*/}
        {/*<circle cx={arrowEndX} cy={arrowEndY} r="10" fill="#456" stroke="none"/>*/}
        {/*<path d={`M ${arrowStartX} ${arrowStartY} ${toX} ${toY}`} fill="none" stroke="#456"/>*/}
        <path
            d={`M ${arrowStartX} ${arrowStartY} C ${arrowStartX + tension} ${arrowStartY} ${arrowEndX} ${arrowEndY - tension} ${arrowEndX} ${arrowEndY - 10}`}
            fill="none"
            stroke="#456"
            marker-end="url(#triangle)"/>
    </svg>;
}

function BlockAnswer({answer, isSelected, handleArrowMountClick, handleTextChange}) {
    let className = null;

    if (isSelected) {
        className = 'd_answer__selected';
    } else if (answer.nextBlockId !== null) {
        className = 'd_answer__filled';
    }

    return <div className="d_block__answer">
        <input value={answer.text}
               onChange={event => handleTextChange(event.target.value)}/>
        <div ref={answer.ref}
             onClick={() => handleArrowMountClick(answer.id)}
             className={className}/>
    </div>;
}

function Block({
                   title,
                   blockId,
                   stepOptions,
                   containerRef,
                   forceRedraw,
                   addOption,
                   handleClickOnBlock,
                   handleClickOnAnswer,
                   selectedAnswerId,
                   handleAnswerTextChange,
               }) {
    return <Draggable
        onDrag={forceRedraw}
        onStop={forceRedraw}
        handle=".d_block__title"
        grid={[1, 1]}
    >
        <div className="d_block" ref={containerRef} onClick={() => handleClickOnBlock()}>
            <p className="d_block__title">
                == {title} ==
            </p>
            {_.map(stepOptions, answer =>
                <BlockAnswer key={answer.id}
                             answer={answer}
                             isSelected={selectedAnswerId === answer.id}
                             handleArrowMountClick={handleClickOnAnswer}
                             handleTextChange={(value) => handleAnswerTextChange(blockId, answer.id, value)}
                />)}
            <button onClick={addOption}>+ option</button>
        </div>
    </Draggable>;
}

class GraphContainer extends Component {
    constructor(props) {
        super(props);

        this.state = {
            blocks: {
                "1": {
                    title: "Block 1",
                    ref: React.createRef(),
                    stepOptions: [{id: "3", text: "Next", ref: React.createRef(), nextBlockId: "2"},],
                }, '2': {
                    title: "Block 2", ref: React.createRef(), stepOptions: [],
                },
            }, selectedAnswerId: null,
        };
    }

    addBlock = () => {
        const newBlockId = smallUUID();
        const block = {
            title: "Block " + newBlockId, ref: React.createRef(), stepOptions: [],
        };

        this.setState({blocks: {...this.state.blocks, [newBlockId]: block}});
    }

    addStepOption(blockId) {
        const block = this.state.blocks[blockId];
        const answerId = smallUUID();
        block.stepOptions = block.stepOptions.concat([{
            id: answerId, text: `Answer ${answerId}`, ref: React.createRef(), nextBlockId: null,
        }]);

        this.setState({
            blocks: {
                ...this.state.blocks, [blockId]: block,
            },
        });
    }

    handleClickOnAnswer(answerId) {
        console.log(answerId);
        if (this.state.selectedAnswerId === answerId) {
            this.setState({selectedAnswerId: null});
        } else {
            this.setState({selectedAnswerId: answerId});
        }
    }

    handleClickOnBlock(clickedBlockId) {
        console.log("ASDBG", clickedBlockId, this.state.selectedAnswerId);
        if (this.state.selectedAnswerId === null) {
            return;
        }

        const blocks = {};
        _.forEach(this.state.blocks, (block, blockId) => {
            blocks[blockId] = block;
            blocks[blockId].stepOptions = _.map(block.stepOptions, option => {
                if (option.id === this.state.selectedAnswerId) {
                    return {...option, nextBlockId: clickedBlockId};
                }
                return option;
            })
        });

        console.log(blocks);

        this.setState({blocks, selectedAnswerId: null});
    }

    handleChangeAnswerText(blockId, answerId, newText) {
        const block = this.state.blocks[blockId];
        for (let i = 0; i < block.stepOptions.length; i++) {
            if (block.stepOptions[i].id !== answerId) {
                continue;
            }

            block.stepOptions[i].text = newText;
            break;
        }

        this.setState({
            blocks: {
                ...this.state.blocks, [blockId]: block,
            },
        });
    }

    componentDidMount() {
        // setInterval(() => {
        //     this.addBlock(); //redraw
        // }, 5000);
        this.setState({});
    }

    render() {
        const blocks = _.flatMap(this.state.blocks, (elem, blockId) => <Block
            key={elem.title}
            title={elem.title}
            containerRef={elem.ref}
            forceRedraw={() => this.setState({})}
            stepOptions={elem.stepOptions}
            addOption={() => this.addStepOption(blockId)}
            blockId={blockId}

            handleClickOnBlock={() => this.handleClickOnBlock(blockId)}
            handleClickOnAnswer={(answerId) => this.handleClickOnAnswer(answerId)}
            selectedAnswerId={this.state.selectedAnswerId}
            handleAnswerTextChange={(...args) => this.handleChangeAnswerText(...args)}
        />);

        const arrows = _.flatMap(this.state.blocks, (block) => _.map(block.stepOptions, (stepOption) => {
            const fromRef = stepOption.ref;
            const toRef = stepOption.nextBlockId && this.state.blocks[stepOption.nextBlockId].ref;
            return <Arrow fromRef={fromRef} toRef={toRef}/>;
        }),);

        return <div className="App" style={{height: "100vh", width: "100%"}}>
            <button onClick={() => this.addBlock()}><h3>+ block</h3></button>
            {blocks}
            {arrows}
        </div>;
    }
}

function App() {
    return <GraphContainer/>;
}

export default App;
